import React, { Component } from 'react'
import PNB from '../../Layouts/Home/images/Fixed Deposit/PNB Housing Finance Ltd..svg'
import HDFC from '../../Layouts/Home/images/Fixed Deposit/HDFC Limited.svg'
import ICICI from '../../Layouts/Home/images/Fixed Deposit/ICICI Home Finance Company Ltd.svg'
import Shriram from '../../Layouts/Home/images/Fixed Deposit/Shriram Transport Finance Limited.svg'
import Bajaj from '../../Layouts/Home/images/Fixed Deposit/Bajaj Finance Limited.svg'
import Mahindra from '../../Layouts/Home/images/Fixed Deposit/Mahindra Finance Limited.svg'
import { withTranslation } from 'react-i18next'

export class PartnerFD extends Component {
    render() {
       const{t}=this.props;
        return (
            <div className="ourPartCaptital">
               <div className="container container-wrapper invest">
                  <div className="row">
                     <div className="col-lg-12">
                        <h2>Our Partners</h2>
                     </div>
                     <div className="col-lg-4 mb-4">
                        <div className="card h-100">
                           <div className='card-body'>
                              <img src={PNB} />
                              <h4>PNB Housing Finance Ltd.</h4>
                              <p>Know More About PNB Housing Finance Ltd.</p>
                           </div>
                           <div className="card-footer pt-0">
                              <a href="/fixed-deposit/pnb-housing-finance-ltd/" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                           </div>
                        </div>
                     </div>
                     <div className="col-lg-4 mb-4">
                        <div className="card h-100">
                           <div className='card-body'>
                              <img src={HDFC} />
                              <h4>HDFC Limited</h4>
                              <p>Know More About HDFC Limited</p>
                           </div>
                           <div className="card-footer pt-0"><a href="/fixed-deposit/hdfc-ltd" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a></div>
                        </div>
                     </div>
                     <div className="col-lg-4 mb-4">
                        <div className="card h-100">
                           <div className='card-body'>
                              <img src={ICICI} />
                              <h4>ICICI Home Finance Company Ltd</h4>
                              <p>Know More About ICICI Home Finance Company Ltd</p>
                           </div>
                           <div className="card-footer pt-0"><a href="/fixed-deposit/icici-home-finance-co-ltd" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a></div>
                        </div>
                     </div>
                     <div className="col-lg-4 mb-4">
                        <div className="card h-100">
                           <div className='card-body'>
                              <img src={Shriram} />
                              <h4>Shriram Transport Finance Limited</h4>
                              <p>Know More About Shriram Transport Finance Limited</p>
                           </div>
                           <div className="card-footer pt-0"><a href="/fixed-deposit/shriram-transport-finance-company-ltd" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a></div>
                        </div>
                     </div>
                     <div className="col-lg-4 mb-4">
                        <div className="card h-100">
                           <div className='card-body'>
                              <img src={Bajaj} />
                              <h4>Bajaj Finance Limited</h4>
                              <p>Know More About Bajaj Finance Limited</p>
                           </div>
                           <div className="card-footer pt-0"><a href="/fixed-deposit/bajaj-finance-ltd" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a></div>
                        </div>
                     </div>
                     <div className="col-lg-4 mb-4">
                        <div className="card h-100">
                           <div className='card-body'>
                              <img src={Mahindra} />
                              <h4>Mahindra Finance Limited</h4>
                              <p>Know More About Mahindra Finance Limited</p>
                           </div>
                           <div className="card-footer pt-0"><a href="/fixed-deposit/mahindra-finance-limited/" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a></div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
        )
    }
}

export default withTranslation() (PartnerFD)
